/*
* npc
* */
import { getConfig } from './methods';
import util from '@util';
import Engine from '../index';

let uid = 1;

class Npc extends Engine {

  constructor(opts = {}) {
    super();
    this.$options = util.extend(getConfig(), opts);
    init.call(this);
  }

  // && 
  getTasks() {
    const game = this.$game,     //game 
      tasks = game.getTasks(),         //
      my = game.my.role,
      level = my.level,       //
      tasked = my.tasks.result;    //

    const _tasks = [];

    util.each(this.tasks, (taskId) => {

      const task = tasks[taskId],           //
        prevTask = task.prevTasks;  //

      // && level
      if (tasked.indexOf(taskId) !== -1 || task.level > level) return;

      //
      //
      if (prevTask) {
        if (tasked.indexOf(prevTask) !== -1) {
          _tasks.push(task);
        }
      } else {
        _tasks.push(task);
      }
    });

    return _tasks;
  }

  //
  getRes() {
    const allRes = this.$game.getRes();
    return util.each(this.res, (resUid) => {
      return allRes[resUid];
    }, true);
  }

  //npc
  resetTaskStatus() {
    this.taskStatus = {
      hasTask: false,           //
      hasUnfinishedTask: false, //
      hasResultTask: false      //
    };
  }
}

function init() {
  this.$map(this, this.$options);
  this.id = (this.id || uid++);
  delete this.$options;
}

export default Npc;


