/*
* 
* */

import util from '@util';

//get equipment config
export function getConfig() {
  return {
    classify: null,     // head,hand,body,foot,weapon
    type: 2,            //
    quality: 1,         //
    property: {
      hp: {
        intrinsic: 0,
        _intrinsic: 0
      },
      mp: {
        intrinsic: 0,
        _intrinsic: 0
      },
      physicalAttack: {
        intrinsic: 0,
        _intrinsic: 0
      },
      physicalDefense: {
        intrinsic: 0,
        _intrinsic: 0
      },
      magicAttack: {
        intrinsic: 0,
        _intrinsic: 0
      },
      magicDefense: {
        intrinsic: 0,
        _intrinsic: 0
      }
    },
    strengthen: 0,         //
    count: 1,            //1()
    isEquip: false        //
  }
}

//update equipment property
export function updateEquipment(equipment) {
  const property = equipment.property;
  util.each(property, (_property) => {
    _property._intrinsic = (_property.intrinsic + _property.intrinsic * (equipment.strengthen * 0.01));
  });
  return equipment;
}


