/*
* 
* */
import { getConfig } from './methods';
import util from '@util';
import Res from '../index';

//  
class Strengthen extends Res {
  constructor(opts = {}) {
    super(util.extend(getConfig(), opts));
    init.call(this);
  }
}

function init() {
  this.$map(this, this.$options);
  delete this.$options;
}

export default Strengthen;


