/*
*   @name {String}
*   @description {String}
*   @classify {Number}  1: 2. 3.
*   @type {Number}  1: 2.&|
*   @status {Number}  0: 1: 2: 3
*   @level {Number}  
*   @reward {Object}
*    @exp {Number} 
*    @currency {Number} 
*    @res {Array}    
* */

export function getConfig() {
  return {
    name: '',           //
    classify: 1,        //
    level: 1,           //
    status: 0,          //

    //
    description: {
      notReceivedTask: "",              //
      unfinishedTask: "",               //
      resultTask: ""                    //
    },

    type: 1,                            //

    details: {                          //
      res: [
        /*{
          id:1,
          count:10
        }*/
      ],                       //
      monsters: [
        /*{
          id:1,
          count:1
        }*/
      ]//
    },
    reward: {           //
      exp: 0,           //
      currency: 0,       //
      res: [
        /*{
          id:1,
          count:1
        }*/
      ]             // uid{count:0} uid + 
    },
    prevTasks: null        //  currentTaskId : prevTaskId
  };
}


