export function getConfig() {
  return {
    name: '',           //
    description: '',    //
    type: 3,            //
    hasUse:false,      //
    property: {
      hp: {
        gain: 0,      //
        effect: 0,     //
        time: null,   //
        timer: null   //
      },
      mp: {
        gain: 0,
        effect: 0,
        time: null,
        timer: null
      },
      exp: {
        gain: 0,
        effect: 0,
        time: null,
        timer: null
      },
      physicalAttack: {
        gain: 0,
        time: null,
        timer: null
      },
      physicalDefense: {
        gain: 0,
        time: null,
        timer: null
      },
      magicAttack: {
        gain: 0,
        time: null,
        timer: null
      },
      magicDefense: {
        gain: 0,
        time: null,
        timer: null
      }
    }
  }
}


